/* Base styles */

.footer {
  background: linear-gradient(to right, #040505, #0a4e5e);
  color: #F0F0F0;
  padding: 20px;
  text-align: center;
  animation: fadeIn 2s ease-out;
}

.footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact-section,
.links-section {
  width: 100%;
}

.contact-section input,
.contact-section button {
  margin-top: 10px;
  padding: 10px;
  width: 80%;
  border: 2px solid #00A4CC;
  border-radius: 5px;
  background-color: transparent;
  color: #F0F0F0;
}

.contact-section input {
  transition: border-color 0.3s ease;
}

.contact-section input:focus {
  border-color: #00C7F2;
  outline: none;
}

.contact-section button {
  background-color: #0084A0;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.contact-section button:hover {
  background-color: #00A4CC;
  transform: translateY(-2px);
}

.links-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 20px;
}

.link-column {
  margin: 10px;
  text-align: left;
}

.link-column h3 {
  margin-bottom: 10px;
  color: #00D4FF;
}

.link-column ul {
  list-style-type: none;
  padding: 0;
}

.link-column ul li {
  transition: color 0.3s ease;
}

.link-column ul li:hover {
  color: #00C7F2;
  cursor: pointer;
}

.footer-bottom {
  margin-top: 20px;
  border-top: 1px solid #ffffff50;
  padding-top: 10px;
  width: 100%;
}

.faGoogle,
.faFacebook,
.faTwitter,
.faPinterest,
.faLinkedin {
  color: #F0F0F0; /* Ensuring icons match the text color */
  transition: color 0.3s ease;
}

.faGoogle:hover,
.faFacebook:hover,
.faTwitter:hover,
.faPinterest:hover,
.faLinkedin:hover {
  color: #00C7F2; /* Light blue color for hover state */
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.icons-list {
  margin: 20px;
}

.iconss:hover {
  transform: scale(1.1);
}

/* Responsive styles */

@media only screen and (max-width: 768px) {
  /* Adjust styles for tablets and smaller devices */
  .footer-content {
    flex-direction: column;
  }

  .link-column {
    text-align: center;
  }

  .icons-list {
    display: flex;
    justify-content: center;
  }
}

@media only screen and (max-width: 480px) {
  /* Adjust styles for mobile phones */
  .contact-section input,
  .contact-section button {
    width: 100%;
  }
}
