:root {
  --primary-gradient-start: #000000;
  --primary-gradient-end: #000000;
  --animation-gradient-start: #0f0f1f;
  --animation-gradient-end: #ffffff;
}

.header-card {
  background: linear-gradient(145deg, var(--primary-gradient-start), var(--primary-gradient-end));
  border-radius: 10px;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.25), 0 20px 25px rgba(0, 0, 0, 0.2);
  position: relative;
  overflow: hidden;
  color: #0c0505;
}

.header-card::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(145deg, var(--animation-gradient-start), var(--animation-gradient-end));
  animation: rotate 15s linear infinite;
  z-index: 0;
}

.header-card * {
  position: relative;
  z-index: 1;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.button {
  background-color: #000000;
  color: wheat;
  padding: 20px 30px;
  margin-right: 10px;
  cursor: pointer;
  border-radius: 8px;
  position: relative;
  overflow: hidden;
  transition: color 0.3s, transform 0.3s;
  font-size: 16px;
  outline: none;
}

.button::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(145deg, var(--animation-gradient-start), var(--animation-gradient-end));
  mix-blend-mode: screen;
  opacity: 0;
  transition: opacity 0.3s;
}

.button:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  color: white;
  background: linear-gradient(to bottom, rgb(0, 0, 0), rgb(255, 30, 30));
}

.button:active {
  transform: translateY(1px);
}

.logo {
  width: 150px;
  border: 2px white;
}

/* Responsive Styles */
@media only screen and (max-width: 768px) {
  .header-card {
    flex-direction: column;
    align-items: center;
  }
  
  .button {
    margin-top: 10px;
  }
}
