@import url('https://fonts.googleapis.com/css?family=Hind:300,400&display=swap');

.faq {
  background: linear-gradient(to bottom, #04040f, rgb(34, 24, 80));
  height: 100vh;
  perspective: 1000px; /* Add perspective to give depth */
}

/* Variables */
:root {
  --bg: #fff;
  --text: #7288a2;
  --gray: #4d5974;
  --lightgray: #e5e5e5;
  --blue: #03b5d2;
}

.containerfaq {
  margin: 0 auto;
  padding: 4rem;
  width: 708px;
}

/* Accordion styles */
.accordion-item {
  border-bottom: 1px solid var(--lightgray);
  transition: transform 0.3s ease; /* Smooth transition for 3D effect */
}

.accordion-item:hover {
  transform: translateY(-5px) scale(1.03); /* Slight lift and scale */
  box-shadow: 0 10px 20px rgba(0,0,0,0.2); /* Soft shadow for depth */
}

.accordion button {
  position: relative;
  display: block;
  text-align: left;
  width: 100%;
  padding: 1em 0;
  color: var(--text);
  font-size: 1.15rem;
  font-weight: 400;
  border: none;
  background: none;
  outline: none;
  cursor: pointer;
}

.accordion button:hover,
.accordion button:focus {
  color: var(--blue);
}

.accordion button:hover::after,
.accordion button:focus::after {
  border: 1px solid var(--blue);
}

.accordion .accordion-title {
  padding: 1em 1.5em 1em 0;
}

.accordion .icon {
  display: inline-block;
  position: absolute;
  top: 18px;
  right: 0;
  width: 22px;
  height: 22px;
  border: 1px solid;
  border-radius: 22px;
  margin-top: 10px;
  color: azure;
}

.accordion .icon::before,
.accordion .icon::after {
  display: block;
  position: absolute;
  content: '';
  background: currentColor;
}

.accordion .icon::before {
  top: 9px;
  left: 5px;
  width: 10px;
  height: 2px;
}

.accordion .icon::after {
  top: 5px;
  left: 9px;
  width: 2px;
  height: 10px;
}

.accordion button[aria-expanded='true'] {
  color: var(--blue);
}

.accordion button[aria-expanded='true'] .icon::after {
  width: 0;
}

.accordion .accordion-content {
  opacity: 0;
  max-height: 0;
  overflow: hidden;
  transition: opacity 200ms linear, max-height 200ms linear;
  background: rgba(255, 255, 255, 0.1); /* Slightly see-through */
  border-left: 5px solid var(--blue); /* Add a colored edge for depth */
  padding: 0 20px; /* Adjust padding */
  box-shadow: inset 0 0 10px rgba(0,0,0,0.3); /* Inner shadow for inset effect */
}

.accordion button[aria-expanded='true'] + .accordion-content {
  opacity: 1;
  max-height: 9em;
  transition: all 200ms linear;
}

.accordion .accordion-content p {
  font-size: 1rem;
  font-weight: 300;
  margin: 2em 0;
  color: wheat;
}
