.card-container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 20px;
  }
  
  .card {
    background:linear-gradient(to bottom, rgb(227, 29, 29),rgb(0, 0, 0));
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 170px; /* Adjusted padding */
    margin-bottom: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2), /* Base shadow */
                0 0 20px rgba(0, 0, 0, 0.1) inset; /* 3D effect */
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease, -webkit-transform 0.3s ease-in-out; /* Smooth transition effect */
    position: relative; /* For pseudo-elements */
    overflow: hidden; /* Ensures pseudo-elements stay within the card */
  }
  

  .card:hover {
    transform: scale(1.05); /* Slightly larger scale for hover */
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2), /* Enhanced shadow for depth */
                0 0 20px rgba(0, 0, 0, 0.1) inset; /* Maintained inset shadow for 3D effect */
  }
  
  .left-card {
    flex: 1;
    margin-right: 20px;
    height: 380px;
  }
  
  .right-cards {
    flex: 2;
    display: flex;
    flex-direction: column;
  }
  @keyframes pulse {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.02);
    }
    100% {
      transform: scale(1);
    }
  }
  .card {
    /* Pulse animation added here */
    animation: pulse 2s infinite ease-in-out;
    /* Rest of the .card styles */
  }
  
  .card:hover:before {
    /* This will now combine both the opacity transition and the rotation animation */
    animation: rotateGradient 5s infinite linear;
    opacity: 1; /* Make sure this doesn't interrupt the animation */
  }
  
  /* Keyframes for pulse */
  @keyframes pulse {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.02);
    }
    100% {
      transform: scale(1);
    }
  }
  
  /* Keyframes for rotateGradient */
  @keyframes rotateGradient {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
  @media only screen and (max-width: 768px) {
    .card-container {
      flex-direction: column;
    }
    
    .card {
      width: 100%;
      margin-right: 0;
      margin-bottom: 10px;
    }
    
    .left-card {
      margin-right: 0;
      margin-bottom: 10px;
    }
  }
  